import { UserAttributes } from "../types";
import { createDataElementHead, formatDate } from "./helpers";

// Function to display user information card
export function renderProfileCard(user: UserAttributes) {
    const appD = document.getElementById("welcome_info");
    if (!appD) {
        console.error("Profile container not found!");
        return;
    }

    const app = document.getElementById("topDiv");
    if (!app) {
        console.error("Profile container not found!");
        return;
    }

    const welcomeMessage = document.createElement("h2");
    welcomeMessage.textContent = `Welcome, ${user.firstName ?? ''} ${user.lastName ?? ''}!`;
    welcomeMessage.classList.add("welcome-message");

    appD.append(welcomeMessage);

    const wrapper = document.createElement("div");
    wrapper.classList.add("wrapper");

    const left = document.createElement("div");
    left.classList.add("left");

    const userName = document.createElement("h4");
    userName.textContent = `üë§ ${user.firstName ?? 'N/A'} ${user.lastName ?? 'N/A'}`.trim();

    left.appendChild(userName);

    const usernameField = document.createElement("p");
    usernameField.textContent = `üìõ Username: ${user.username ?? 'N/A'}`;
    left.appendChild(usernameField);

    const campusField = document.createElement("p");
    campusField.textContent = `üìç Campus: ${user.campus ?? 'N/A'}`;
    left.appendChild(campusField);

    const userRole = document.createElement("p");
    userRole.textContent = `üéì Qualification: ${user.qualification ?? "No qualification provided"}`;
    left.appendChild(userRole);

    const right = document.createElement("div");
    right.classList.add("right");

    const info = document.createElement("div");
    info.classList.add("info");
    const infoTitle = document.createElement("h3");
    infoTitle.textContent = "Information";
    const infoData = document.createElement("div");
    infoData.classList.add("info_data");

    infoData.appendChild(createDataElementHead("üì© Email", user.email ?? "N/A"));
    infoData.appendChild(createDataElementHead("üìû Phone", user.PhoneNumber ?? "N/A"));
    infoData.appendChild(createDataElementHead("üåç Country", user.addressCountry ?? "N/A"));
    infoData.appendChild(createDataElementHead("‚ößÔ∏è Gender", user.genders ?? "N/A"));
    infoData.appendChild(createDataElementHead("üÜî CPR Number", user.CPRnumber ?? "N/A"));
    infoData.appendChild(createDataElementHead("üéÇ Date of Birth", formatDate(user.dateOfBirth) ?? "N/A"));
    infoData.appendChild(createDataElementHead("üìú Degree", user.Degree ?? "N/A"));

    info.appendChild(infoTitle);
    info.appendChild(infoData);

    right.appendChild(info);

    wrapper.appendChild(left);
    wrapper.appendChild(right);

    app.append(wrapper);
}
